                        </div>
                        <div class="tamanoTipoDocumento">
                            <input type="text" v-model="idDocumento">
                        </div>
                    </div>
                    <div class="col row" v-if="option == 'juridico'">
                        <div class="titles d-flex justify-content-start" >
                            RUT
                        </div>
                        <div class="tamanoTipoDocumento">
                            <input type="text" v-model="idDocumento" >
                        </div>
                    </div>
                    <div class="row">
                            <div class="titles d-flex justify-content-start" >
