                            </div>
                    </div>
                </div>
                <b-button id="ADDDeudor" @click="add()">Agregar Deudor</b-button>
            </b-modal>    
        </div>
        
        <table class="table">
            <thead class="encabezadoTabla">
                <tr>
                <th scope="col">TIPO DE PERSONA</th>
                <th scope="col">IDENTIFICADOR</th>
                <th scope="col">NOMBRE/RAZON</th>
                </tr>
            </thead>
            <tbody class="bodyTabla" v-if="items.length == 0">
                <td>No data</td>
            </tbody>
            <tbody class="bodyTabla" v-else v-for="(item,index) in items" :key="index">
                <td>{{item.Tipo}}</td>
                <td>{{item.Id}}</td>
                <td>{{item.Name}}</td>
            </tbody>
 
        </table> 
         
    </div>
</template>

<script>
export default {
  name: 'AcreedorFormularios',
  data() {
        return {
            option:'natural',
            headers: ['Tipo', 'Id', 'Name'],
            items: [{}],
            idDocumento:"",
            nombrePersona: "",
        }
    },

    methods:{
        changeOption(){
            var selectBox = document.getElementById("tipoDePersonaDeudor");
            this.option = selectBox.options[selectBox.selectedIndex].value; 
        },
        add() {
            let item = {
                "Tipo": this.option,
                "Id": this.idDocumento,
                "Name": this.nombrePersona}
            this.items.push(item);
        }
    }

}

</script>


